// Home.js

import React, { Component } from 'react';

import BanquetBanner from './banquetBanner';
import BanquetGallery from './banquetGallery';
import Guidelines from './guidlines';
import BanquetMenu from './banquetMenu';
import BanquetBannerFooter from './Home/banquetBannerFooter';

class Banquet extends Component {
  render() {
    return (
        <div>
              
            
       <BanquetBanner/>
       <BanquetGallery/>
       <Guidelines/>
       <BanquetMenu/>
       <BanquetBannerFooter/>
        </div>
    );
  }
}

export default Banquet;