import React from "react";
import { link } from "react-router-dom";



const CulinaryAbout = () => (

  <div ClassName="container">
    
    <div id="our-school" class="container">
    <div className="row">
      <div className="col text-center">
        <div className="story-title">
          <h2 className="mt-3">About Our Program</h2>
          <div className="underline mb-5"></div>
        </div>
      
      </div>
      
    </div>
    
    <div className="row">
      <div className="col-md-10 mx-auto">
        <p>We have created a comprehensive 15months excellerated Culinary Arts Program that will equip every student with the skills to excell in any area of the Culinary Arts field from business, management, becoming a chef, Master Pastry Chief, Executive Chef, Personal Chief, Wine Sommelier, restaurant business owner,or a Culinary arts instructor.</p><p> We offer a 100% job placement guarantee whn each student completes the program with opportunities of advancement. So if you love to cook we know we can help be apart of the process of helping you meet your goals of success.</p>

        <h4>(UMEP) Give Back Scholarship</h4>
        <p> Every year (UMEP) gives a total of 25 students the opportunity to attend our Culinary Arts School
        100% tuition free. Students are selected on a needs bases. To apply students must write a 1000 word essay explanning why they want to attend our school. If selected these students must continue there employment with us for a total of two years. Graduated students will have the opportunity to work in various sector of the industry directly on the grounds of the facility. If students do not complete the deal of working with us after graduation. Students will be required to pay back the tuition of the program with interest.</p>
       <p>To apply for the (UMEP) Scholarship click the button below. </p>
        <p><a className="btn btn-green" href="./Culinary">Apply Here</a></p>
      

       <h4>Registration for the Summer Session 2020</h4>
       <p>Early Registration May 11, 2020</p>
       <p>Registraton June 7, 2020 </p>
       <p>Late Registration July 5, 2020</p>
       <p>Classs begins July 5, 2020</p>
        <p>To register for the upcoming summer session click the button below. </p>
        <p><a className="btn btn-green" href="./Culinary">Register Here</a></p>
        
      </div>
    
      <div className="col-lg-12 mt-5">
      <hr/>
      
      </div>
     </div>
  
  </div>
</div>








);

export default CulinaryAbout;